*{
  font-family: 'Poppins', sans-serif;
}
.App{
height: 100vh;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
}
#quote-box{
  padding: 20px ;
  width: 40vw;
  height:auto;
  background-color: whitesmoke;
  border-radius: 10px;
}
#text{
  padding: 0 20px;
  margin-bottom: 0;
  margin-top: 7px;
  text-align: center;
  font-size:30px;
}
#author{
  text-align: right;
}

#new-quote{
  float: right;
  color: white;
  border: 1px solid;
  border-radius: 5px;
  padding: 10px 15px;

}
#tweet-quote{
  background-color: white;
  width: 10px;
  border-radius: 5px;
  padding: 10px 15px;

}
#author,#text{
  color:#6930c3;
}
.App, #tweet-quote ,#new-quote{
  background-color: #6930c3;
}
#by{
color: #001524;
text-align: center;
letter-spacing: 5px;
}


